// centralize pace

// make components that dynamically are built from Calclet. like <Calclet distance="42.2"> for marathon
// <Calclet distance="1.6"> for mile pace
// <Calclet distance="1"> for 1km pace

class Calclet extends React.Component {

  touchStart = 0;
  touchSensitivity = 3;

  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.handleKeyDown = this.handleKeyDown.bind(this);
    this.handleTouchStart = this.handleTouchStart.bind(this);   
    this.handleTouchMove = this.handleTouchMove.bind(this);
  }

  handleChange(e) {    
    this.props.change(e.target.value, this.props.index);
  }

  handleKeyDown(e) {    
    this.props.keyDown(e, this.props.index);
  }

  handleTouchStart(e) {
    this.touchStart = e.touches[0].clientY;       
  }

  handleTouchMove(e) {
   
    var touchEnd = e.touches[0].clientY;

    if(this.touchStart > touchEnd +this.touchSensitivity){
      console.log("up!");
      this.touchStart = touchEnd;
      this.props.indecrement(1,this.props.index);
      
    } else if(this.touchStart < touchEnd -this.touchSensitivity){
      console.log("down!");
      this.touchStart = touchEnd;
      this.props.indecrement(-1,this.props.index);
    }   

  }

  render () {  
    const value = this.props.value;
    return (
      <div>              
        <TextField       
          label={this.props.label}                   
          value= {value}        
          onChange={this.handleChange} 
          onKeyDown={this.handleKeyDown}
          onTouchStart={this.handleTouchStart}         
          onTouchMove={this.handleTouchMove}
          margin="normal"
          variant="outlined"
          fullWidth = {true}
        />                  
      </div>   
    );
  }

}


class AddButton extends React.Component {

  constructor(props) {
    super(props);
    this.handleClick= this.handleClick.bind(this);
    this.handleChange= this.handleChange.bind(this);

    this.state = {      
      distance: props.distance,
    }
  }

  handleClick(e) {
    this.props.onClick(this.state.distance);
  }
  handleChange(e) {
    this.setState({
      distance: e.target.value,      
    })
  }

  render () {
    return (
      <div class="adder">
        <TextField 
          label="Distance to Add"               
          value= {this.state.distance}        
          onChange={this.handleChange} 
          margin="normal"
          variant="outlined"
          fullWidth = {false}
        />
        <Button variant="outlined" onClick = {this.handleClick} > 
          Add {this.state.distance}km
        </Button>
      </div>
    );
  }
}

class Calculator extends React.Component {

  calclets = [
    {
      distance: 1, 
      label: "Pace 1km",
    },
    {
      distance: 1.60934, 
      label: "Pace 1 mile",
    },
    {
      distance: 0.8, 
      label: "800m",
    }, 
    {
      distance: 2*1.60934, 
      label: "2 mile",
    },
   
    {
      distance: 5,          
    },
    {
      distance: 10,          
    },
    {
      distance: 42.195/2, 
      label: "Half Marathon"    
    },
    {
      distance: 42.195, 
      label: "Marathon"    
    },
    {
      distance: 50,          
    },
  
  ];

  constructor(props) {
    super(props);
    this.change = this.change.bind(this);
    this.indecrement = this.indecrement.bind(this);
    this.keyDown = this.keyDown.bind(this);
    this.addCalclet = this.addCalclet.bind(this);

    this.state = {      
      calclets: [],
    }

    for (let i = 0; i < this.calclets.length; i++) {
      let calclet = this.calclets[i];
      this.state.calclets[i] = {
        index: i,
        distance: calclet.distance,        
        value: '',
      }
    }
    
  } 

  addCalclet(distance) {
    
    var calclets = this.state.calclets.slice(); 
    calclets.push({
      distance: distance,
      index: calclets.length,
      value: '',
    });
    
    this.calclets.push({
      distance: distance,
    });
    this.setState({
      calclets: calclets,      
    })       
    
  }

  change(timeTyped, i) {   
    var calclets = this.state.calclets.slice();    
    calclets[i].value = timeTyped;    

    const timeS = HMStoSeconds(timeTyped);    
    var pace = timeS/calclets[i].distance;   

    this.updateTimesNotI(pace, calclets, i)
    
    this.setState({
      calclets: calclets,      
    })
    
  }

  indecrement(delta, i) {
    var calclets = this.state.calclets.slice();     
    
    if (calclets[i].value == "") {
      calclets[i].value = "0:00";
      this.setState({
        calclets: calclets,      
      })
    }

    var timeS = HMStoSeconds(calclets[i].value);
    
    timeS += delta;
    if (timeS < 0) timeS = 0;

    calclets[i].value = secondsToHMS(timeS); 
    var pace = timeS/calclets[i].distance;      
    this.updateTimesNotI(pace, calclets, i);

    this.setState({
      calclets: calclets,      
    })

  }

  updateTimesNotI(pace, calclets, i) { // utility to update times, except i
    
    for (var j in calclets) {
      if (j != i) {
        if (isNaN(pace))
          calclets[j].value = '';
        else 
          calclets[j].value = secondsToHMS(pace*calclets[j].distance);                    
      }
    }   

  }

  keyDown(e, i) {   
    var calclets = this.state.calclets.slice();     
    
    if (calclets[i].value !== "") { 
      var timeS = HMStoSeconds(calclets[i].value); 
      if (e.keyCode == 38)  // up key
        timeS += 1;             
      else if (e.keyCode == 40) // down key
        timeS -= 1;             
      else return; // ignore all other key presses here 

      calclets[i].value = secondsToHMS(timeS); 
      
      var pace = timeS/calclets[i].distance;   
      this.updateTimesNotI(pace, calclets, i);

      this.setState({
        calclets: calclets,      
      })
      e.preventDefault();
      e.stopPropagation();
      return false;

    }
    // else console.log("value: ",calclets[i].value, calclets)
   
    //else console.log(keyDown);
  
  }

  renderCalclet(id) {   
    
    return (
      <Calclet
        distance={this.calclets[id].distance}       
        change={this.change}  
        keyDown={this.keyDown}  
        value={this.state.calclets[id].value}  
        index={id} 
        key={id}      
        label={this.calclets[id].label ? this.calclets[id].label : this.calclets[id].distance + "km"}
        indecrement={this.indecrement}        
      />
    );
  }

  renderCalclets() {
    console.log(this.state.calclets);
    let arr = []    
    for (var i = 0; i < this.state.calclets.length; i++) {
      arr.push(<GridListTile> {this.renderCalclet(i)}</GridListTile> );
    }    
    return <GridList cols={3} cellHeight={80}> { arr }</GridList>;
  }

  render () {
    return (
      <div class="full-outer">      
        <div class = "full-inner">
          <Grid container justify = "left">
            <PaperSheet 
              title = "Dr. Ward's famous multi-directional Pace Calculator"
              body = "Type in the time in any field, all of the rest will calculate. All fields are live! Times should follow the format HH:MM:SS. Examples of valid times: 5:30, 45:20 or 1:59:59"  
            />     
          </Grid>
        
          { this.renderCalclets() }   

          <AddButton onClick = {this.addCalclet} distance = {9}/>
     
        </div>       
      </div>
    );
  }

}

function secondsToHMS(seconds) {       
    
    var hours   = Math.floor(seconds / 3600);
    var minutes = Math.floor((seconds- (hours * 3600)) / 60);
    var seconds = Math.round(seconds - (hours * 3600) - (minutes * 60));

    var str = "";
    if (hours > 0) {     
      str = hours + ":"
    }
    if (hours > 0 || minutes > 0) {
      if (minutes < 10 && hours > 0 ) {
        minutes = "0"+minutes
      }            
      str = str + minutes + ":";              
    }
    if (minutes == 0  && hours == 0)
      str = "0:";
    if (seconds < 10) {seconds = "0"+seconds} 
    str = str + seconds;  
    
    return str;   

}

function HMStoSeconds(hms) { 

  var a = hms.split(/[^\d]+|:/g); // split it at the colons
  
  if (a.length > 3) return;
  if (a.length == 3)
    // minutes are worth 60 seconds. Hours are worth 60 minutes.
    return (+a[0]) * 60 * 60 + (+a[1]) * 60 + (+a[2]);
  if (a.length == 2)
    return (+a[0]) * 60 + (+a[1]);
  if (a.length == 3)
    return (+a[0]);

}

